
import {Vue,Component,Prop,PropSync,Watch} from 'vue-property-decorator';
import SvgIcon from '@/components/svgIcon/index.vue';
import CircleIcon from './indexIcon.vue';
import Service from '@/https/physical/list'
@Component({
    components:{
        SvgIcon,
        CircleIcon
    }
})
export default class BusinessTest extends Vue{
    @PropSync('visible')visible_sync!:boolean;
    @Prop({default:()=>({
        az_id:'',
        az_name:'',
    })}) az_info!:any
    private taskList:Array<any>= [];
    private selectedTasks:Array<any> = [];
    private task:string='';
    private selectable:any=[];
    private first:any={};
    private second:any={};
    private third:any={}
    created(){
        this.get_task_list()
    }
    private async get_task_list(){
        let res:any=await Service.get_task_list({})
        if(res.code==='Success'){
            this.taskList = [...res.data.task_list];
            this.taskList.map(item=>{
                if(!['CreateEcsGpuInstanceForTestPipeline','DeleteEcsGpuInstanceForTestPipeline','UpdateHostForTestPipeline'].includes(item.maintask_name)){
                    this.selectable.push(item)
                }
                if(item.maintask_name==='CreateEcsGpuInstanceForTestPipeline'){
                    this.first = item;
                }
                if(item.maintask_name==='DeleteEcsGpuInstanceForTestPipeline'){
                    this.second = item;
                }
                if(item.maintask_name==='UpdateHostForTestPipeline'){
                    this.third = item;
                }
                return item;
            })

        }
    }
    private set_disList(){
        let ids = this.selectedTasks.map(task=>task.task_id)//id
        this.selectedTasks.map(item=>{
            item.disList = ids.filter(inn=>inn!==item.maintask_name)
            return item;
        })
    }
    private add(){
        if(this.selectedTasks.length===this.selectable.length){
            return;
        }
        this.selectedTasks.push({task_id:'',params:{},disList:[]})
        this.set_disList()
    }
    private changeTask(val,key){
        this.set_disList()
    }
    private del(ind){
        let list:any=[]
        this.selectedTasks.map((item,index)=>{
            if(index!==ind){
                list.push(item)
            }
            return item;
        })
        this.selectedTasks = list
        this.set_disList()
    }
    private setParams(){

    }
    private async confirm(){
        let list=[
            {...this.first},
            // {maintask_name:this.taskList[1].maintask_name,dep_params:{}},
        ]
        this.selectedTasks.map(item=>{
            list.push({maintask_name:item.task_id,dep_params:item.params})
            return item;
        })
        list.push(this.second)
        list.push(this.third)
        let res:any = await Service.create_test_task({
            az_id:this.az_info.az_id,
            task_list:list
        })
        if(res.code==='Success'){
            this.$message.success(res.message);
        }
        this.visible_sync=false
    }
    private cancel(){
        this.visible_sync=false
    }
}
