
import { Component, Emit, Prop, Vue,PropSync } from 'vue-property-decorator';
import ActionBlock from '@/components/search/actionBlock.vue';
import Service from '@/https/physical/list';
import ClusterService from '@/https/vmOp2/cluster/pod/index'
import moment from 'moment';
import {Form} from 'element-ui'
import bus from "@/utils/vmOp2/eventBus";
import { findPodIdByClusterId } from "@/utils/vmOp2/findPodId"
@Component({
  components:{
    ActionBlock
  }
})
export default class Operate extends Vue{
  $message;
  @PropSync('visible') visible_sync!:Boolean;
  @Prop({default:()=>[]}) rows!:any
  @Prop(String) title!:string;
  @Prop(String) oper_type!:string;
  private alert_title = `${this.rows.length}${this.title}`
  private status_list:Array<String> = ['','',''];
  private flag_list:Array<String> = ['','',''];
  private valid:number=1;
  private isFlag:number=1;
  private selectVmList =[]
  private notSelectVmList =[]
  private list:any=[]
  private cluster_list:any =[]
  private labelObj={
    'schedule':'',
    'migrate_flag':'',
    'cheat':'',
  }
  private op_typ_list={
    'schedule':'set_host_scheduled',
    'migrate_flag':'set_host_migrated',
    'cheat':'set_host_dummy',
  }
  private op_typ_req={
    'schedule':'scheduled',
    'migrate_flag':'migrated',
    'cheat':'dummy',
  }
  private valueObj={
    'schedule':['',''],
    'migrate_flag':['',''],
    'cheat':['',''],
  }
  private recycle_list=[]
  private form_data={
    valid:'',
    recycleId:'',
    reason:'',
    isSet:this.oper_type==='cheat' ? '0' : '1',
    toCluster:''
  }
  private operate_info={
    'start_up_host':'host_operate',
    'shutdown_host':'host_operate',
    'restart_host':'host_operate',
    'online_maintenance':'host_maintenance',
    'offline_maintenance':'host_maintenance',
    'finish':'finish_maintenance',
    'shelves':'shelves',
    'disperse':'disperse',
    'finish_validate':'finish_validate',
    'schedule':'set_flag',
    'migrate_flag':'set_flag',
    'cheat':'set_flag',
    'under_sync':'update_ecs_info',
    'data_clear': 'crash_clear',
    'down_recover': 'crash_recover',
    'lock':'set_lock',
    'unlock':'set_unlock',
    'maintenance':'set_maintenance',
    'change_cluster':'add_cluster',
    'unstore_exception':'recover_storage_error',
    'set_prepar_host':'set_prepare_host',
    'set_resource_pools':'set_prepare_host'
  }
  private created() {
      ['shelves','finish_validate'].includes(this.oper_type) && this.get_host_recycle_department()
      if(this.oper_type === 'change_cluster'){
        this.getClusterList()
      }

    if(this.oper_type === 'maintenance'){
      this.rows.forEach(row => {
        if (row.machine_status === 'lock' && row.ecs_count === 0 ){
          this.selectVmList.push(row)
        } else {
          this.notSelectVmList.push(row)
        }
      })
      this.list = this.selectVmList.map(item => {
        return {...item, maintenanceReason: ''};
      });
    } else if(this.oper_type === 'lock'){
      this.list = this.rows.map(item => {
        return {...item, lockReason: ''};
      });
    } else {
      this.list = []
      this.list = [...this.list,...this.rows]
    }
  }
  private async getClusterList(){
    let podID = findPodIdByClusterId(this.$route.params.id)
    let reqData = {
      page_index: 1,
      page_size: 1000,
      az_id:this.$store.state.az_id,
      pod_id:this.$route.name === 'pod_host' ? this.$route.params.id : podID
    }
    let res:any = await ClusterService.get_pod_cluster_list(reqData)
    if(res.code === 'Success'){
      this.cluster_list = res.data.result
    }
  }
  private async get_host_recycle_department(){
    let res:any = await Service.get_host_recycle_department({})
    if(res.code==="Success"){
      this.recycle_list = res.data
    }
  }
  private async confirm(){
    console.log(this.rows[0].host_id,'-------',this.oper_type)
    if(['shelves','finish_validate'].includes(this.oper_type)){
      let flag:boolean=true
      let form = this.$refs.form as Form;
      form.validate((valid)=>{
        if(!valid){
          flag = false
        }
      })
      if(!flag){
        return false
      }
    }
    if (this.oper_type === 'maintenance') {
      let flagReason = this.list.some(item => !item.maintenanceReason);
      if (flagReason) {
        return false
      }
    }
    if (this.oper_type === 'lock') {
      let flagReason = this.list.some(item => !item.lockReason);
      if (flagReason) {
        return false
      }
    }
    const maintenance_detail = this.list.map(item=>{return {host_id:item.host_id,reason:item.maintenanceReason}})
    const lock_detail = this.list.map(item=>{return {host_id:item.host_id,reason:item.lockReason}})
    const change_cluster = this.list.map(item=>{return {host_id:item.host_id,cluster_id:this.form_data.toCluster}})
    const remove_cluster = this.list.map(item=>{return {host_id:item.host_id,cluster_id:'0-0'}})
    const set_type = this.oper_type === 'set_prepar_host' ? 'prepare_host' : 'resource_pool'
    let req=this.status_list.includes(this.title) ? {
      op_type:this.oper_type,
      host_ids:this.rows.map(item=>item.host_id)
    }:['schedule','migrate_flag','cheat'].includes(this.oper_type) ? {
      host_ids:this.rows.map(item=>item.host_id),
      [this.op_typ_req[this.oper_type]]:Number(this.form_data.isSet),
      description:this.form_data.reason,
      op_type:this.op_typ_list[this.oper_type]
    } : ['online_maintenance','offline_maintenance'].includes(this.oper_type) ? {
      maintenance_type:this.oper_type,
      host_ids:this.rows.map(item=>item.host_id)
    }:this.oper_type==="finish_validate" ? {
      host_ids:this.rows.map(item=>item.host_id),
      status:this.form_data.valid==='1' ? 'READY' : 'INIT_ERROR',
      noticer:this.form_data.recycleId,
      reason:this.form_data.valid==='0' ?this.form_data.reason : undefined,
    } : this.oper_type==="shelves" ? {
      host_ids:this.rows.map(item=>item.host_id),
      department_name:this.form_data.recycleId
    } : this.oper_type==="under_sync" ? {
      pod_id: this.$store.state.pod_id,
      host_ecs: {}
    } : this.oper_type==="maintenance" ? {
        maintenance_detail:maintenance_detail,
        host_ids:this.list.map(item=>item.host_id)
      }:this.oper_type==="lock" ? {
      lock_detail:lock_detail,
      host_ids:this.list.map(item=>item.host_id)
      }:this.oper_type === 'change_cluster'?{
      param:change_cluster
    }:this.oper_type === 'remove_cluster'?{
      param:remove_cluster,
      is_move_out:'1'
    }:this.oper_type ==='set_prepar_host' || this.oper_type === 'set_resource_pools' ?{
      host_id:this.rows[0].host_id,
      set_type:set_type
    }: {host_ids:this.rows.map(item=>item.host_id)}

    // 
    if(this.oper_type==="under_sync") {
      for(let i of this.rows){
        req.host_ecs[i.host_id] = []
        if(i.ecs_list.length > 0) {
          i.ecs_list.map(item => {
            req.host_ecs[i.host_id].push(item.ecs_id)
          })
        }
      }
    }
    let res :any ={}
    if(this.oper_type!=="change_cluster" && this.oper_type!=="remove_cluster" ){
      res=await Service[this.operate_info[this.oper_type]]({
        ...req
    })
    }else {
      res=await ClusterService.add_cluster({
        ...req
      })
    }

    if(res.code==="Success"){
      if(['finish_validate','disperse','under_sync','set_prepar_host','set_resource_pools'].includes(this.oper_type)){
        this.$message.success(res.message)
        this.back("1")
      }else if(['data_clear','down_recover','change_cluster',"remove_cluster"].includes(this.oper_type)) {
        if(res.data.fail_host_list.length>0) {
          this.$message.warning(res.message + '' + res.data.error_msg)
          this.back("0");
          return;
        } else {
          this.$message.success(res.message)
          this.back("1")
          if(['change_cluster',"remove_cluster"].includes(this.oper_type)){
            bus.$emit('getTreeData',false)
          }
        }
      } else{
        if(res.data.fail_host_list.length>0){
          if(this.oper_type === 'maintenance'){
            let message = "";
            for (let key in res.data.fail_msg_group_by_host) {
              message += res.data.fail_msg_group_by_host[key] + "\n";
            }
            this.$message.error(message);
            this.back("0");
          }else {
            this.$message.warning(res.message + res.data.fail_msg)
            this.back("0");
            return;
          }
        }else{
          this.$message.success(res.message)
          this.back("1")
        }
      }
    }else{
      this.back("0")
    }
  }
  @Emit("close")
  private back(val){
      this.visible_sync=false
  }

}
