
import {Component, Vue, Watch} from "vue-property-decorator";
import ActionBlock from '@/components/search/actionBlock.vue';
import SvgIcon from '@/components/svgIcon/index.vue';
import SearchFrom from "@/components/search/searchFrom.vue";
import CustomListItem from '@/views/physical/customListItem.vue';
import {deal_list} from "@/utils/transIndex";
import RightClick from "@/views/vmOp2/component/right-click.vue";
import Service from "@/https/vmOp2/cluster/pod";
import { rightClick } from "@/utils/vmOp2/rightClick"
import { hideMenu} from "@/utils/vmOp2/hideMenu"
import {getHostStatus} from "@/utils/getStatusInfo";
import {Table} from "element-ui";
import Operate from "@/components/vmOp2/cluster/pod/host/operate.vue"
import UploadFile from "@/components/vmOp2/cluster/pod/host/upload.vue"
import Migrate from '@/components/vmOp2/cluster/pod/host/migrate.vue';
import Record from '@/components/vmOp2/cluster/pod/host/record.vue';
import Resource from '@/components/vmOp2/cluster/pod/host/resource.vue';
import BusinessTest from '@/components/vmOp2/cluster/pod/host/businessTest.vue';
import Remark from '@/components/vmOp2/cluster/pod/host/editRemark.vue';
import moment from "moment";
import Set_label from "@/views/vmOp2/cluster/host/set_label.vue";
import HostService from '@/https/physical/list';

@Component({
  components: {
    Set_label,
    RightClick,
    SearchFrom,
    SvgIcon,
    CustomListItem,
    Operate,
    UploadFile,
    Migrate,
    Record,
    Resource,
    BusinessTest,
    Remark,
    ActionBlock
  }
})

export default class HostList extends Vue{
  private list=[]
  private all_column_item=[];
  private multi_rows:any=[];
  private search_data:any={}
  private az_info:any={}
  private visible:Boolean=false;
  private oper_type:string="";
  private oper_label:string="";
  private power_list=[];
  private filter_data:any={}
  private machine_list=[]
  private power_status:any=""
  private machine_status:any =''
  private filter_info:any={}
  private sort_prop_name = '';
  private sort_order = undefined;
  private page_info:any={
    current:1,
    size:20,
    total:0
  }
  private all_item:Array<any>=[]
  private custom_host=[]
  private show_custom:boolean=false;
  private rows_operate_btns:any=[
    {label:'',value:'upload'},
    {label:'',value:'service'},
    {label:'',value:'rollback'},
  ]
  private is_pare_host=[
    {text:'',value:'1'},
    {text:'',value:'0'}
  ]
  private search_option:any={
    out_band_address:{placeholder:'IP'},
    host_ip:{placeholder:'IP'},
    host_name:{placeholder:''},
    host_id:{placeholder:'ID'},
    vm_id:{placeholder:'ID'},
    cpu:{placeholder:'CPU'},
    gpu_model:{placeholder:''},
    nic:{placeholder:''},
    room:{placeholder:'',list:[]},
    host_rack:{placeholder:''},
    bare_metal_id:{placeholder:'ID'},
    bare_metal_name:{placeholder:''},
    label:{placeholder:'',list:[]},
    create_time: {
      placeholder: ['', ''],
      type: 'datetimerange',
      width: '360',
      clearable: true,
      dis_day: 31,
      defaultTime: []
    },
  }
  private switch_power:any= [
      {label:'',value:'start_up_host',disabled:false},
      {label:'',value:'shutdown_host',disabled:false},
    {label:'',value:'restart_host',disabled:false},
  ]
  private lock_machine:any =[
    {label:'',value:'lock',disabled:false},
    {label:'',value:'unlock',disabled:false}
  ]
  private crash_list:any=[
    {label:'',value:'data_clear',disabled:false},
    {label:'',value:'down_recover',disabled:false}
  ]
  private maintenance_host:any =[
    {label:'',value:'maintenance',disabled:false},
    {label:'',value:'finish',disabled:false},
  ]
  private sign_host:any =[
    {label:'',value:'schedule',disabled:false},
    {label:'',value:'migrate_flag',disabled:false},
  ]
  private get menus() {
    const base_list= [
      {label: '', value: 'migrate', single: true, disabled: false},
      {label: '', value: 'record', single: true, disabled: false},
      {label: '', value: 'resource', single: true, disabled: false},
      {label: '', value: 'remark', single: true, disabled: false},
      {label: '', value: 'start_or_shutdown', list: this.switch_power, disabled: false},
      {label: '', value: 'lock_machine', disabled: false, list: this.lock_machine},
      {label: '', value: 'change_cluster', disabled: false},
      {label: '', value: 'remove_cluster', disabled: false},
      {label: '', value: 'maintenance', disabled: false, list: this.maintenance_host},
      {label: '', value: 'crash', disabled: false, list: this.crash_list},
      {label: '', value: 'sign', disabled: false, list: this.sign_host},
      {label: '', value: 'under_sync', disabled: false},
      {label: '', value: 'disperse', disabled: false},
      {label: '', value: 'cheat', disabled: false},
      {label: '', value: 'business_test', disabled: false},
      {label: '', value: 'finish_validate', disabled: false},
      {label: '', value: 'unstore_exception', disabled: false},
      {label: '', value: 'service', disabled: false},
      {label: '', value: 'rollback', disabled: false},
      {label:'',value:'set_label',single:true,}
    ]
    if(this.multi_rows.length>0){
      if(this.multi_rows[0].is_prepare_host){
        base_list.push({label: '', value: 'set_resource_pools', disabled: false})
      }else {
        base_list.push({label: '', value: 'set_prepar_host', disabled: false})
      }
    }
    return base_list
  }
  private error_msg={
    start_up_host:'',
    shutdown_host:'',
    restart_host:'',
    finish:'',
    shelves:'',
    disperse:'',
    migrate:'',
    unlock:'',
    data_clear:'',
    down_recover:'!',
    remove_cluster:'',
    unstore_exception:'',
    set_prepar_host:'',
    set_resource_pools:''
  }
  @Watch('multi_rows',{immediate:true,deep:true})
  private watch_multi(){
    this.handleMenus()
  }
  created(){
    this.get_room_list()
    this.get_field()
    this.get_pod_host_list()
    this.get_status_list()
    this.get_label_list()
  }
  private async get_label_list() {
    this.search_option.label.list = []
    let res:any = await Service.get_all_host_label({
    })
    if(res.code==='Success'){
      res.data.all_host_label_list.forEach(item => {
        this.search_option.label.list.push({
          label:item.label_name,
          type:item.label_name
        })
      })
    }
  }
  private async get_room_list(){
    let res:any=await HostService.get_room_list({})
    if(res.code==="Success"){

      res.data.machine_room.forEach(item => {
        this.search_option.room.list.push({
          label:item,
          type:item
        })
      })
    }
  }
  private fn_search(data:any={}){
    this.search_data = {...data};
    this.page_info.current = 1;
    this.judgeColumns()
    this.get_pod_host_list();

  }
  private close(val){
    //this.oper_type==="upload" && this.get_room_list()
    this.oper_type === 'set_label' && this.get_label_list()
    val==='1' && this.get_pod_host_list()
    this.visible=false;
    this.oper_type='';
    this.oper_label=""
    this.multi_rows=[]
    const table =this.$refs.table as Table
    table.clearSelection()
  }
  private handleMenus() {
    // 
    for (const item of this.menus) {
      if (!item.list) {
        item.disabled = this.handle(item.label, item.value);
      } else {
        // 
        for (const inn of item.list) {
          inn.disabled = this.handle(inn.label, inn.value);
        }
      }
    }
  }
  private handle(label,value){
    if(value==='finish_validate'){
      if(this.multi_rows.every(item=>['init_error','init','offline'].includes(item.machine_status))){
        return false
      }else{
        this.error_msg['finish_validate'] = ''!
        return true
      }
    }
    if(value === 'remove_cluster'){
      if(this.multi_rows.every(item=> item.cluster_id === '0-0' )){
        return true
      }
    }
    if(['upload','resource','update_attribute','business_test','schedule','migrate_flag','cheat','under_sync'].includes(value)){
      if(value==='business_test'){
        if(this.list.length===0){
          this.error_msg['business_test']='!'
          return true
        }
      }
      return false
    }
    if(value === 'set_prepar_host' || value ==='set_resource_pools'){
      const is_prepare_host = this.multi_rows.some(item => item.is_prepare_host)
      const not_prepare_host = this.multi_rows.some(item => !item.is_prepare_host)
      return is_prepare_host && not_prepare_host
    }
    if(this.judge(value)){
      if(value==="disperse"){
        //GPU
        let fil = this.multi_rows.filter(item=>item.host_purpose==='GPU');
        let bool:boolean = fil.every(item=>{//GPUGPU
          return item.ecs_list.every(inn=>(inn.is_gpu && inn.status==="") || !inn.is_gpu)
        })
        if(!bool){
          this.error_msg['disperse']="GPUGPU!"
          return false
        }
      }
    }else{
      return  true
    }
  }
  private judge(val):any{
    const obj = getHostStatus(val)
    let flag_list = this.multi_rows.every(item=>{
      let power_flag =obj.power.length===0 ? true : obj.power.includes(item.power_status)
      let host_flag =obj.host.length===0 ? true : obj.host.includes(item.machine_status)
      let vm_flag= obj.vm ? obj.vm=== item.ecs_count + 1 : true;
      // if(!vm_flag && ['shutdown_host'].includes(val)){
      //   this.error_msg[val]=getHostStatus(val).msSg2
      // }else{
      //   return false
      // }
      return power_flag && host_flag && vm_flag
    })
    return flag_list
  }
  private infoClick(item) {
    const {label, value}=item
    if(!item.list && !item.disabled){
      if(value==='business_test'){
        this.az_info={
          az_id:this.$store.state.az_id,
          az_name:this.$store.state.az_list.az_name,
        }
      }
        this.oper_type = value;
        this.oper_label = label
        this.visible = true;
      hideMenu()
    }
  }
  @Watch("$store.state.az_id")
  private watch_az(nv){
    if(!nv){
      return;
    }
    this.refresh()
  }
  private refresh(){
    this.get_pod_host_list()
  }
  //
  private FnOperRow(row){
   (this.$refs.table as any).toggleRowSelection(row)
  }
  //
  rowStyle({row}) {
    const isSelected = this.multi_rows.some(item => item.host_id === row.host_id);
      if (isSelected) {
        return 'rowStyle'
      }
  }
  //
  FnRightClick(row,column,event){
    //
    const isSelected = this.multi_rows.some(item => item.host_id === row.host_id);
    if (!isSelected) {
      (this.$refs.table as any).toggleRowSelection(row)
    }
    rightClick(row,column,event)
  }
  private async get_field(){
    let res:any = await Service.get_pod_host_field()
    if(res.code==="Success"){
    let key_list=['field_name','show_name'];
    let label_list=['prop','label'];
    let list:Array<any>=[]
    this.all_item = res.data.map(item=>{
      list=[...list,...item.filed];
      return item;
    })
      this.all_column_item = deal_list(list,label_list,key_list);
      this.get_custom_columns(this.$store.state.pod.host_list)
      this.FnDoLayout()
    }
  }
  private get_custom_columns(list) {
    if(list.length===0){
      return;
    }
    this.custom_host = this.all_column_item.filter(item=>list.includes(item.label));//
    this.custom_host.map((item:any)=>{
      if(!['cpu_model','gpu_model','bare_metal_name','power_status_name','machine_status_name','is_prepare_host'].includes(item.prop)) {
        item = Object.assign(item, {}, {sortable: 'custom'})
      }
      if(['host_id','bare_metal_name','cpu_model'].includes(item.prop)){
        item = Object.assign(item,{},{width:'240px',overflow:true})
      }
      if(['host_name','gpu_model'].includes(item.prop)){
        item = Object.assign(item,{},{width:'150px',overflow:true})
      }
      if(['host_ip','out_band_address','gpu_count','ecs_count','cpu_rate','ram_rate','gpu_rage','machine_status_name','power_status_name'].includes(item.prop)){
        item = Object.assign(item,{},{minWidth:'120px'})
      }
      if(item.prop==='power_status_name'){
        item = Object.assign(item,{},{column_key:'power_status',list:this.power_list})
      }
      if(item.prop==='machine_status_name'){
        item = Object.assign(item,{},{column_key:'machine_status',list:this.machine_list})
      }
      if(item.prop==='is_prepare_host'){
        item = Object.assign(item,{},{column_key:'is_prepare_host',list:this.is_pare_host})
      }
      return item;
    })
    this.FnDoLayout()
  }
  private FnDoLayout(){
    this.$nextTick(()=>{
      (this.$refs.table as any).doLayout()
    })
  }
  private async get_status_list(){
    let res:any=await Service.get_status_list({})
    if(res.code==="Success"){
      for(let i in res.data.power_status){
        this.power_list.push({text:res.data.power_status[i],value:i})
      }
      for(let i in res.data.machine_status){
        this.machine_list.push({text:res.data.machine_status[i],value:i})
      }
    }
  }
  private FnCustom() {
    this.show_custom = true;
  }
  private async down(){
    const {
      room,
      host_name,
      vm_id,
      out_band_address,
      host_ip,
      host_id,
      gpu_model,
      host_rack,
      sort_host_ip,
      create_time,
      cpu,
      nic,
      bare_metal_name,
      bare_metal_id,
      label,
    }=this.search_data
    let obj = {
      //pod_id:this.$route.params.id,
      machine_room_name:room,
      host_name,
      vm_id,
      out_band_address,
      host_ip,
      host_id,
      gpu_model,
      host_rack,
      sort_host_ip,
      cpu,
      nic,
      bare_metal_name,
      bare_metal_id,
      label,
      start_time:create_time && create_time[0] ? moment(create_time[0]).format('YYYY-MM-DD HH:mm:ss') : undefined,
      end_time:create_time && create_time[1] ? moment(create_time[1]).format('YYYY-MM-DD HH:mm:ss') : undefined,
      ...this.filter_info,
      field_names:JSON.stringify(this.custom_host.map((item:any)=>item.prop))
    }
    if(this.$route.name === 'pod_host'){
      obj['pod_id'] = this.$route.params.id
    }
    if(this.$route.name === 'cluster_host'){
      obj['cluster_id'] = this.$route.params.id
    }
    let str=""
    for (let i in obj){
      if(obj[i]){
        str =str+`${i}=${obj[i]}&`
      }
    }
    let query = str==="" ? "" : `?${str.slice(0,str.length-1)}`
    window.location.href=`/ecs_business/v1/host/host_list_download/${query}`
  }
  private async get_pod_host_list(){
    if(!this.$store.state.az_id){
      return
    }
    const {
      room,
      host_name,
      vm_id,
      out_band_address,
      host_ip,
      host_id,
      gpu_model,
      host_rack,
      create_time,
      cpu,
      nic,
      bare_metal_name,
      bare_metal_id,
      vgpu_segment_type,
      host_info,
      label,
    }=this.search_data;
    let reqData = {
      host_name,
      vm_id,
      out_band_address,
      host_ip,
      host_id,
      gpu_model,
      host_rack,
      cpu,
      nic,
      bare_metal_name,
      bare_metal_id,
      host_info,
      label,
      machine_room_name:room,
      vgpu_segment_type: vgpu_segment_type ? vgpu_segment_type[0] : undefined,
      start_time:create_time && create_time[0] ? moment(create_time[0]).format('YYYY-MM-DD HH:mm:ss') : undefined,
      end_time:create_time && create_time[1] ? moment(create_time[1]).format('YYYY-MM-DD HH:mm:ss') : undefined,
      page_index: this.page_info.current,
      page_size: this.page_info.size,
      az_id:this.$store.state.az_id,
      [this.sort_prop_name]: this.sort_order,
      ...this.filter_info
    }
    if(this.$route.name === 'pod_host'){
      reqData['pod_id'] = this.$route.params.id
    }
    if(this.$route.name === 'cluster_host'){
      reqData['cluster_id'] = this.$route.params.id
    }
    let res:any = await Service.get_pod_host_list(reqData)
    if(res.code === 'Success'){
      this.list = res.data.result
      this.page_info.total = res.data.page_info.count
    }
  }
  //
  private judgeColumns(){
    // let keys = Object.keys(this.filter_data)
    // let temp = ['power_status','machine_status','is_prepare_host']
    // keys.map(item=>{
    //   if(!temp.includes(item)){
    //     delete(this.filter_data[item])
    //   }
    // });
    this.filter_info={}
    for(let i in this.filter_data){
      this.filter_info[i]=this.filter_data[i][0]
    }
  }
  private filterAttribute(obj:any){
    this.filter_data = {...this.filter_data,...obj}
    this.judgeColumns()
    this.get_pod_host_list()
  }
  private handleSelectionChange(data){
    this.multi_rows = data
  }
  private FnSortChange(val){
    let relation = {};
    this.all_item.forEach(item => {
      item.filed.forEach(inn => {
        relation[inn.field_name] = `sort_${inn.field_name}`;
      });
    });
    this.sort_prop_name = relation[val.prop];
    this.sort_order = val.order === "ascending" ? '0' : val.order === "descending" ? '1' : undefined;
    this.get_pod_host_list()
  }
  private handleSizeChange(size){
    this.page_info.size = size
    this.get_pod_host_list()
  }
  private handleCurrentChange(cur){
    this.page_info.current = cur
    this.get_pod_host_list()
  }
}
